import ScrollAnimation from 'react-animate-on-scroll'

class AnimatedText extends React.Component {

    createCharsAnimated = (text) => {
        const chars = []
        console.log("TEXT: ", text)
        for (let i = 0; i < text.lenght; i++) {
            console.log('charAt: ', text.charAt(i))
            chars.push(<ScrollAnimation key={i} animateIn="slideInDown" animateOnce delay={i * 200}>{text.charAt(i)}</ScrollAnimation>)
        }
        console.log(chars)
        return chars
    }

    render() {
        const { text }= this.props

        return (
            <span>
                {
                    this.createCharsAnimated(text)
                }
            </span>
        )
    }
}

export default AnimatedText